
(function () {
    function monitorScroll() {
        this.fnDelay = this.bind(this, this.flag, 10);
        this.iOptionHeight();
        // this.addHandler(document,' DOMContentLoaded',this.iOptionHeight)
        this.addHandler(window, 'scroll', this.fnDelay);
        this.arr_color = ["#ffa809", "#22b573", "#9c58b6", "#26b1b6", "#5A4446", "#ed6461"];
    }
    monitorScroll.prototype = {
        bind(obj, func, wait) {
            let timer = null;
            return function () {
                let context = obj;
                clearTimeout(timer);
                timer = setTimeout(function () {
                    func.apply(context);
                }, wait)
            };
        },
        iOptionHeight() {
            // this.addHandler(document, "readystatechange", function (event) {
            //     alert(document.readyState);
            //     alert(document.getElementById('skill'))
            // });
            // let doc = document;
            // let arr_offset = [];
            // let iBasicHeight = doc.getElementById('basic').offsetTop;
            // let iSkillHeight = doc.getElementById('skill').offsetTop;
            // let iProHeight = doc.getElementById('pro').offsetTop;
            // let iExpHeight = doc.getElementById('exp').offsetTop;
            // let iEstHeight = doc.getElementById('est').offsetTop;
            // let iConHeight = doc.getElementById('con').offsetTop;
            // arr_offset.push(iSkillHeight, iBasicHeight, iProHeight, iExpHeight, iEstHeight, iConHeight);
            this.arr_offset = [];
            var _this = this;
            setTimeout(function () {
                let doc = document;
                let iBasicHeight = doc.getElementById('basic').offsetTop - 85;
                let iSkillHeight = doc.getElementById('skill').offsetTop - 85;
                let iProHeight = doc.getElementById('pro').offsetTop - 85;
                let iExpHeight = doc.getElementById('exp').offsetTop - 85;
                let iEstHeight = doc.getElementById('est').offsetTop - 85;
                let iConHeight = doc.getElementById('con').offsetTop - 85;
                // let iBasicHeight = doc.getElementById('basic').offsetTop;
                // let iSkillHeight = doc.getElementById('skill').offsetTop;
                // let iProHeight = doc.getElementById('pro').offsetTop;
                // let iExpHeight = doc.getElementById('exp').offsetTop;
                // let iEstHeight = doc.getElementById('est').offsetTop;
                // let iConHeight = doc.getElementById('con').offsetTop;
                _this.arr_offset.push(iBasicHeight, iProHeight, iSkillHeight, iExpHeight, iEstHeight, iConHeight);
            }, 0);
        },
        flag() {
            let iScroll = document.documentElement.scrollTop || document.body.scrollTop || window.pageYOffset
            let iClient = document.documentElement.clientHeight || document.body.clientHeight;
            let documentScroll = document.documentElement.scrollHeight || document.body.scrollHeight;
            let iOffset = document.getElementById('skill').offsetTop;
            let rotate = document.getElementsByClassName('rotate')[0];
            let navbar = document.getElementsByClassName('navbar')[0];
            let oPara = navbar.getElementsByTagName('p');
            // if ((iScroll + iClient - 100) > iOffset) {
            //     document.getElementsByClassName('rotate')[0].classList.add('isRotate')
            // }
            (iScroll + iClient - 100) > iOffset ? rotate.classList.add('isRotate') : 0;
            iScroll > iClient ? navbar.classList.add('fixed') : navbar.classList.remove('fixed');
            oPara[0].style.backgroundColor = "";
            for (let i = 0; i < this.arr_offset.length - 2; i++) {
                let heightOne = this.arr_offset[i];
                let heightTwo = this.arr_offset[i + 1];
                if (heightTwo && (iScroll > heightOne && iScroll < heightTwo)) {
                    for (let j = 0; j < oPara.length; j++) {
                        oPara[j].style.background = "";
                    }
                    oPara[i].style.backgroundColor = this.arr_color[i];
                }
                if ((documentScroll - 85) <= iClient + iScroll) {
                    for (let j = 0; j < oPara.length; j++) {
                        oPara[j].style.background = "";
                    }
                    oPara[oPara.length - 1].style.backgroundColor = this.arr_color[this.arr_color.length - 1];
                    oPara[oPara.length - 2].style.backgroundColor = this.arr_color[this.arr_color.length - 2];
                }
            }
        },
        addHandler(obj, type, handle) {
            if (obj.addEventlistener) {
                obj.addEventlistener(type, handle);
            }

            else {
                obj['on' + type] = handle;
            }
        },
        removeHandler(obj, type, handle) {
            if (obj.removeEventListener) {
                obj.removeEventListener(type, handle, false);
            }
            else {
                obj['on' + type] = null;
            }
        }
    }
    new monitorScroll()
}());
(function () {
    setTimeout(function () {
        function lazyLoad(opt) {
            this.init(opt);
        };
        lazyLoad.prototype = {
            init: function (opt) {
                this.setOptions(opt); // 这是初始化函数
                this.load();
                this.fnLoad = this.bind(this, this.load); //bind是全局对象和方法，这里在这个对象里进行调用了
                this.addHandler(window, 'scroll', this.fnLoad);
                this.addHandler(window, 'resize', this.fnLoad);
            },
            setOptions: function (opt) {
                this.holderSrc = opt.holderSrc || 'data-src';
                this.wrapId = opt.wrapId;      //获取body标签
                this.imgList = [];              //声明空的数组列表
                this.timer = null;              //声明空对象
                var targets = null;
                // if (document.querySelectorAll) {
                //     targets = document.querySelectorAll("#" + this.wrapId + " img");
                // } else {
                // console.log(document.getElementById('nav'));
                targets = document.getElementById(this.wrapId).getElementsByTagName("img")
                // }                                           //获取img标签
                var n = 0,
                    len = targets.length;
                // 把元素存到数组里  
                for (; n < len; n++) {                      //遍历长度
                    if (targets[n].getAttribute(this.holderSrc)) {
                        this.imgList.push(targets[n]);      //获取图片的真实路径并且传到数组中去
                    }
                }
                // console.log(this.imgList);
            },
            load: function () {
                // 全部加装，解除事件
                if (this.imgList.length == 0) {  //如果图片长度为0则去除添加在window上的srcoll和resize事件
                    this.removeHandler(window, 'scroll', this.fnLoad);
                    this.removeHandler(window, 'resize', this.fnLoad);
                    return
                }
                var st = document.body.scrollTop || document.documentElement.scrollTop,
                    clientH = document.documentElement.clientHeight,
                    scrollArea = st + clientH;         //获取滚动条的高度和页面的可视高度
                for (var n = 0; n < this.imgList.length; n++) {
                    var offsetTop = this.imgList[n].getBoundingClientRect().top + st,
                        imgH = this.imgList[n].clientHeight;
                    if (scrollArea > (offsetTop - 130) && (imgH + offsetTop) > st) {
                        // if (scrollArea > (offsetTop - 100)) {
                        var _src = this.imgList[n].getAttribute(this.holderSrc);
                        this.imgList[n].setAttribute('src', _src);
                        this.imgList[n].style.opacity = 1;
                        this.imgList[n].style.filter = 'alpha(opacity =' + 100 + ')';
                        this.imgList.splice(n, 1);     //删除已经加载完的元素
                        n--;
                    }
                }

            },
            bind: function (obj, fn) {
                var _this = this;
                return function () {
                    if (_this.timer) clearTimeout(_this.timer);
                    _this.timer = setTimeout(function () {
                        fn.apply(obj, arguments);
                    }, 100);
                    // 这个是做了函数的节流

                }
            },
            addHandler: function (node, type, fn) {
                if (node.addEventListener) {
                    node.addEventListener(type, fn, false);
                }
                else {
                    node.attachEvent('on' + type, function () {
                        fn.apply(node, arguments);
                    });
                }
            },
            removeHandler: function (node, type, fn) {
                if (node.addEventListener) {
                    node.removeEventListener(type, fn, false);
                } else {
                    node.detachEvent("on" + type, fn);
                }
            }
        }
        new lazyLoad({ wrapId: 'app' })
    }, 0)
}()
)



